* {
    box-sizing: border-box;
} 


body {
    margin: 0;
    font-family: 'Monserrat', Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Create a sticky/fixed navbar */
#navbar {
    transition: all 1s ease 0s;
    overflow: hidden;
    background-color: #FF6F91;
     /* Large padding which will shrink on scroll (using JS) */
    position: fixed; /* Sticky/fixed navbar */
    width: 100%;
    height: 100px;
    padding: 0px 0px 220px 0px;
    top: 0; /* At the top */
    z-index: 1;
}

#navbar-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}


#navbar-logo{
    transition: all 1s ease 0s;
    margin: 85px 0px 0px 50px;
    float: left;
}


/* Style the logo */
#navbar-logo a {
    transition: all 1s ease 0s;
    font-size: 60px;
    font-weight: 900;
    color: #744db3;
    letter-spacing: 0.093em;
    text-decoration: none;
    outline-color: #744db3;
    outline-style: dashed;
    outline-width: 8px;
    padding: 0px 12px 3px 12px;
}


/* Display some links to the right */
#navbar-right {
    transition: all 1s ease 0s;
    float: right;
    padding: 133px 50px 0px 0px;
}
  
/* Style the navbar links */
#navbar-right a {
    float: left;
    color: white;
    text-align: center;
    padding: 10px 10px;
    margin: 0px 14px;
    text-decoration: none;
    font-size: 18px;
    line-height: 25px;
    border-radius: 10px;
}
  
/* Links on mouse-over */
#navbar-right a:hover {
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
    border-bottom: 3px solid white;
    position: relative;
    box-shadow: 0px 9px 5px -5px rgba(00,00,00,0.2);
}
  
/* Style the active/current link */
#navbar-right a.active {
    background-color: #744db3;
    box-shadow: 3px 3px 5px 0px rgba(00,00,00,0.2);
}



/* Add responsiveness - on screens less than 759px wide, display the navbar vertically instead of horizontally */
@media screen and (max-width: 759px) {

    #navbar{
        height: 220px; 
        padding: 0px 0px 0px 0px !important;
    }

    #navbar-logo{
        float: left;
        margin: 60px 0px 0px 20px !important;
        text-align: center;
    }

    #navbar-logo a{
        float: none;
        font-size: 40px !important;
        outline-width: 6px !important;
    }

    #navbar-right {
        max-width: 50%;
        position: absolute;
        top: 10px;
        right: 0px;
        float: none;
        padding: 0px 0px 0px 0px !important;
    }

    #navbar-right a {
      float: none;
      display: block;
      text-align: center;
      padding: 8px 50px auto;
      margin: 4px 20px;
    }
}