#contact-wrapper{
    padding: 0px 30px 50px 30px;
}

#contact-container{
    color: white;
    padding-top: 300px;
    margin: 0 auto;
    max-width: 500px;
}

#contact-container h1{
    font-size: 2.3rem;
	margin: 0px;
	text-shadow: 2px 3px 0px #898999;
}

#contact-container ul {
	padding: 0px 0px 0px 30px;
	margin: 0px;
}

#contact-container li {
	margin: 7px 0px;
}

#contact-container a:link{
    color: white;
}


@media screen and (max-width: 759px) {

    #contact-container h1{
        font-size: 1.6rem;
    }

	#contact-container li {
        margin: 18px 0px;
    }

}