body {
	background-color: #59BAB8;
	
   
}

#content {
	max-width: 920px;
	margin: 0px auto;
}

 .picOfMyFace {
	padding: 300px 50px 0px 0px;
	max-width: 37%;
	height: auto;
	float: right;
}

#name-and-jobtitle {
	padding: 400px 0px 0px 20px;
	max-width: 55%;
	float: left;
	text-align: center;
	color: white;
}

#name-and-jobtitle h1 {
	font-size: 2.3rem;
	margin: 0px;
	text-shadow: 2px 3px 0px #898999;
}




#about-me {
	color: white;
	padding: 100px 0px 0px 20px;
	float: left;
	text-align: left;
	max-width: 500px;
}

#about-me h1 {
	text-shadow: 2px 3px 0px #898999;
	margin: 0px;
}

#about-me p {
	margin: 8px 0px;
}





#details {
	text-align: left;
	color: white;
	padding: 50px 0px 0px 90px;
    float: left;
    margin-bottom: 100px;
}

#details h1 {
	text-shadow: 2px 3px 0px #898999;
	padding-bottom: 0px;
	margin: 0px;
}

#details ul {
	padding: 0px 0px 0px 30px;
	margin: 0px;
}

#details li {
	margin: 7px 0px;
}




/* Add responsiveness - on screens less than 759px wide, display the navbar vertically instead of horizontally */
@media screen and (max-width: 759px) {

	.picOfMyFace {
		padding: 250px 20px 0px 0px;
		max-width: 57%;
		height: auto;
		left: 50%; 
	}

	#name-and-jobtitle {
		padding: 0px 0px 0px 15px;
		max-width: 55%;
		left: 50vw; 
	}

	#name-and-jobtitle h1 {
		font-size: 1.5rem;
	}

	#about-me {
		padding: 20px 15px 0px 15px;
	}

	#details {
		padding: 20px 15px 0px 15px;
	}

}