#resume-wrapper{
    padding: 0px 30px 70px 30px;
}

#resume-container {
    padding-top: 300px;
    color: white;
    margin: 0 auto;
    max-width: 700px;
}

#download-button button {
    color: white;
    background-color: #744db3;
    box-shadow: 3px 3px 5px 0px rgba(00,00,00,0.2);
    border: none;
    font-size: 18px;
    line-height: 25px;
    border-radius: 10px;
    text-align: center;
    padding: 10px 10px;
    float: right;
    margin: 0px 0px 50px 0px; 
}


#resume-written{
    clear: both;
}

#resume-written h1 {
	font-size: 2.3rem;
	margin: 0px;
	text-shadow: 2px 3px 0px #898999;
}

#resume-written .resume-section{
    margin: 0px 0px 90px 0px;
}



#resume-written .skill-title{
    float: left;
}

#resume-written .skills-listed{
    float: right;
}


#resume-written .education-title{
    float: left;
}

#resume-written .work-title{
    float: left;
}

#resume-written .competition-title{
    float: left;
}

#resume-written .place{
    float: right;
}

#resume-written .place{
    float: right;
}

#resume-written .date{
    float: left;
}




@media screen and (max-width: 759px) {

    #resume-written h1 {
        font-size: 1.6rem;
    }

    #resume-written .resume-section{
        margin: 0px 0px 110px 0px;
    }


    #resume-written .skills-listed{
        margin-bottom: 15px;
        float: left;
    }

    #resume-written .place{     
        clear: both;
        float: left;
    }

    #resume-written .date{
        clear: both;
        margin-bottom: 15px;
    }

    #resume-written .competition-title{
        clear: both;
    }

    #resume-written .work-title{
        clear: both;
    }

    #resume-written .education-title{
        clear: both;
    }

    #resume-written .skill-title{
        clear: both;
    }

}